import { useCallback, useEffect, useState } from "react";
import { useStudentProfileAboutDetail } from "../../Hooks/ApiHooks/InstituteStudent/institute-student-api";
import BorderBottom from "../../Ui/Border/BorderBottom";
import style from "./StudentProfile.module.css";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import { useTranslation } from "react-i18next";
import Loading from "../../Loader/Loading";
import FormRaggingUnderTaking from "../../../JoiningForm/Student/FormRaggingUnderTaking";
const StudentDynamicProfileAbout = ({ studentId }) => {
  const { t } = useTranslation();
  const [openDialog, setOpenDialog] = useState("");

  const { studentProfileAboutDetail, studentProfileAboutDetailLoading } =
    useStudentProfileAboutDetail({
      data: {
        sid: studentId,
      },
      skip: !studentId,
    });

  const onViewFile = useCallback((iKey) => {
    if (iKey) {
      const newWindow = window.open(
        `${imageShowUrl}/${iKey}`,
        "_blank",
        "noopener,noreferrer"
      );
      if (newWindow) newWindow.opener = null;
    }
  }, []);
  return (
    <>
      {openDialog && (
        <FormRaggingUnderTaking
          onClose={() => setOpenDialog("")}
          value={openDialog}
        />
      )}
      <section className={style.sdpa}>
        {studentProfileAboutDetail?.map((sl, index) => (
          <div className={style.sdpa_section} key={`${sl?.key}${index}`}>
            <h5>
              {sl?.key ?? ""}
              {["student_undertakings", "student_anti_ragging"]?.includes(
                sl?.fields?.[0]?.form_checklist_key
              ) ? (
                <span
                  style={{
                    cursor: "pointer",
                    color: "#00f",
                    marginLeft: "0.3rem",
                  }}
                  onClick={() =>
                    setOpenDialog({
                      label: sl?.fields?.[0]?.form_checklist_name,
                      conent: sl?.fields?.[0]?.value,
                    })
                  }
                >
                  View Sample
                </span>
              ) : null}
            </h5>

            {sl?.static_key === "academic_details" ? (
              <>
                {sl?.fields?.map((fl, f_index) => (
                  <div
                    className={style.sdpa_section}
                    key={`${fl?.form_checklist_key}${f_index}`}
                    style={{
                      marginBottom: "0.5rem",
                    }}
                  >
                    <h5
                      style={{
                        fontSize: "13px",
                        marginBottom: "0.1rem",
                      }}
                    >
                      {fl?.form_checklist_name ?? ""}
                    </h5>
                    {[
                      "std_tenth_details",
                      "hsc_diploma",
                      "fyjc",
                      "fy_sem_I",
                      "fy_sem_II",
                      "sy_sem_III",
                      "sy_sem_IV",
                      "ty_sem_V",
                      "ty_sem_VI",
                      "msc_mcom",
                    ]?.includes(fl?.form_checklist_key) ? (
                      <div className={style.sdpac_wrap}>
                        {fl?.nested_form_checklist?.map((gl, g_index) => (
                          <div
                            className={style.sdpas_container}
                            key={`${gl?.form_checklist_key}${g_index}`}
                          >
                            <h6 className={style.sdpasc_label}>
                              {gl?.form_checklist_name ?? ""} :
                            </h6>
                            <h6 className={style.sdpasc_value}>
                              {gl?.value ?? ""}
                            </h6>
                          </div>
                        ))}
                      </div>
                    ) : (
                      fl?.nested_form_checklist?.map((gl, g_index) => (
                        <div
                          className={style.sdpa_section}
                          key={`${gl?.form_checklist_key}${g_index}`}
                          style={{
                            marginBottom: "0.5rem",
                          }}
                        >
                          <h5
                            style={{
                              fontSize: "13px",
                              marginBottom: "0.1rem",
                            }}
                          >
                            {gl?.form_checklist_name ?? ""}
                          </h5>
                          <div className={style.sdpac_wrap}>
                            {gl?.nested_form_checklist_nested?.map(
                              (tl, t_index) => (
                                <div
                                  className={style.sdpas_container}
                                  key={`${tl?.form_checklist_key}${t_index}`}
                                >
                                  <h6 className={style.sdpasc_label}>
                                    {tl?.form_checklist_name ?? ""} :
                                  </h6>
                                  <h6 className={style.sdpasc_value}>
                                    {tl?.value ?? ""}
                                  </h6>
                                </div>
                              )
                            )}
                          </div>
                        </div>
                      ))
                    )}
                  </div>
                ))}
              </>
            ) : (
              <div className={style.sdpac_wrap}>
                {sl?.fields?.map((fl, f_index) =>
                  fl?.form_checklist_typo === "FILE" ? (
                    <div
                      className={style.sdpas_container}
                      key={`${sl?.form_checklist_key}${f_index}`}
                    >
                      <p className={style.sdpasc_label}>
                        {fl?.form_checklist_lable ?? ""} :
                      </p>
                      {fl?.value ? (
                        <h6
                          className={style.sdpasc_value}
                          onClick={() => onViewFile(fl?.value)}
                          style={{
                            cursor: "pointer",
                            color: "#002BBE",
                          }}
                        >
                          {t("view_file")}
                        </h6>
                      ) : null}
                    </div>
                  ) : [
                      "student_undertakings",
                      "student_anti_ragging",
                      "studentProfilePhoto",
                    ]?.includes(fl?.form_checklist_key) ? null : (
                    <div
                      className={style.sdpas_container}
                      key={`${sl?.form_checklist_key}${f_index}`}
                    >
                      <p className={style.sdpasc_label}>
                        {fl?.form_checklist_lable ?? ""} :
                      </p>
                      <h6 className={style.sdpasc_value}>{fl?.value ?? ""}</h6>
                    </div>
                  )
                )}
              </div>
            )}

            <BorderBottom />
          </div>
        ))}
      </section>
      {studentProfileAboutDetailLoading && <Loading />}
    </>
  );
};

export default StudentDynamicProfileAbout;
